import { describe, it, expect, beforeEach } from '@jest/globals';
import { useUIStore } from '@/lib/stores/uiStore';

describe('UI Store', () => {
  beforeEach(() => {
    // Reset store before each test
    useUIStore.getState().resetUI();
  });

  describe('Basic Functionality', () => {
    it('should initialize with default state', () => {
      const state = useUIStore.getState();
      expect(state.activeTab).toBe('feed');
      expect(state.sidebarOpen).toBe(false);
      expect(state.searchQuery).toBe('');
      expect(state.toasts).toEqual([]);
    });

    it('should update active tab', () => {
      const { setActiveTab } = useUIStore.getState();
      setActiveTab('analytics');
      
      const state = useUIStore.getState();
      expect(state.activeTab).toBe('analytics');
    });

    it('should toggle sidebar', () => {
      const { toggleSidebar } = useUIStore.getState();
      toggleSidebar();
      
      const state = useUIStore.getState();
      expect(state.sidebarOpen).toBe(true);
    });

    it('should add toast notification', () => {
      const { addToast } = useUIStore.getState();
      addToast({
        type: 'success',
        message: 'Test notification'
      });
      
      const state = useUIStore.getState();
      expect(state.toasts).toHaveLength(1);
      expect(state.toasts[0]).toMatchObject({
        type: 'success',
        message: 'Test notification'
      });
    });
  });

  describe('Form State Management', () => {
    it('should manage form data', () => {
      const { setFormData } = useUIStore.getState();
      setFormData('name', 'John Doe');
      
      const state = useUIStore.getState();
      expect(state.formData).toEqual({ name: 'John Doe' });
    });

    it('should handle form errors', () => {
      const { setFormError } = useUIStore.getState();
      setFormError('email', 'Invalid email');
      
      const state = useUIStore.getState();
      expect(state.formErrors).toEqual({ email: 'Invalid email' });
    });

    it('should track form touched state', () => {
      const { setFormTouched } = useUIStore.getState();
      setFormTouched('name', true);
      
      const state = useUIStore.getState();
      expect(state.formTouched).toEqual({ name: true });
    });

    it('should clear form data', () => {
      const { setFormData, clearForm } = useUIStore.getState();
      
      // Set some form data first
      setFormData('name', 'John');
      setFormData('email', 'john@example.com');
      
      clearForm();
      
      const state = useUIStore.getState();
      expect(state.formData).toEqual({});
      expect(state.formErrors).toEqual({});
      expect(state.formTouched).toEqual({});
    });
  });

  describe('Loading States', () => {
    it('should manage loading states', () => {
      const { setLoading } = useUIStore.getState();
      setLoading('submit', true);
      
      const state = useUIStore.getState();
      expect(state.loadingStates).toEqual({ submit: true });
    });

    it('should check loading state', () => {
      const { setLoading } = useUIStore.getState();
      setLoading('fetch', true);
      
      const state = useUIStore.getState();
      expect(state.isLoading('fetch')).toBe(true);
    });

    it('should clear loading state', () => {
      const { setLoading, clearLoading } = useUIStore.getState();
      setLoading('fetch', true);
      clearLoading('fetch');
      
      const state = useUIStore.getState();
      expect(state.loadingStates).toEqual({});
    });
  });

  describe('Error Handling', () => {
    it('should manage error states', () => {
      const { setError } = useUIStore.getState();
      setError('api', 'Network error');
      
      const state = useUIStore.getState();
      expect(state.errors).toEqual({ api: 'Network error' });
    });

    it('should check for errors', () => {
      const { setError, hasError } = useUIStore.getState();
      setError('validation', 'Invalid input');
      
      expect(hasError('validation')).toBe(true);
    });

    it('should clear errors', () => {
      const { setError, clearError } = useUIStore.getState();
      setError('api', 'Network error');
      clearError('api');
      
      const state = useUIStore.getState();
      expect(state.errors).toEqual({});
    });
  });

  describe('Search and Filters', () => {
    it('should manage search query', () => {
      const { setSearchQuery } = useUIStore.getState();
      setSearchQuery('test query');
      
      const state = useUIStore.getState();
      expect(state.searchQuery).toBe('test query');
    });

    it('should manage filters', () => {
      const { setFilter } = useUIStore.getState();
      setFilter('category', 'politics');
      
      const state = useUIStore.getState();
      expect(state.filters).toEqual({ category: 'politics' });
    });

    it('should clear search', () => {
      const { setSearchQuery, setSearchResults, clearSearch } = useUIStore.getState();
      setSearchQuery('test');
      setSearchResults([{ id: 1 }]);
      clearSearch();
      
      const state = useUIStore.getState();
      expect(state.searchQuery).toBe('');
      expect(state.searchResults).toEqual([]);
      expect(state.searchLoading).toBe(false);
    });
  });

  describe('Pagination', () => {
    it('should manage pagination state', () => {
      const { setPagination } = useUIStore.getState();
      setPagination('results', { page: 2, limit: 20, total: 100, hasMore: true });
      
      const state = useUIStore.getState();
      expect(state.pagination).toEqual({
        results: { page: 2, limit: 20, total: 100, hasMore: true }
      });
    });

    it('should navigate to next page', () => {
      const { setPagination, nextPage } = useUIStore.getState();
      setPagination('results', { page: 1, limit: 10, total: 50, hasMore: true });
      nextPage('results');
      
      const state = useUIStore.getState();
      expect(state.pagination.results.page).toBe(2);
    });

    it('should navigate to previous page', () => {
      const { setPagination, prevPage } = useUIStore.getState();
      setPagination('results', { page: 3, limit: 10, total: 50, hasMore: true });
      prevPage('results');
      
      const state = useUIStore.getState();
      expect(state.pagination.results.page).toBe(2);
    });
  });

  describe('Selection Management', () => {
    it('should manage item selection', () => {
      const { selectItem } = useUIStore.getState();
      selectItem('items', 'item1');
      
      const state = useUIStore.getState();
      expect(state.selections).toEqual({ items: new Set(['item1']) });
    });

    it('should toggle item selection', () => {
      const { selectItem, toggleSelection } = useUIStore.getState();
      selectItem('items', 'item1');
      toggleSelection('items', 'item1');
      
      const state = useUIStore.getState();
      expect(state.selections).toEqual({ items: new Set() });
    });

    it('should check if item is selected', () => {
      const { selectItem, isSelected } = useUIStore.getState();
      selectItem('items', 'item1');
      
      expect(isSelected('items', 'item1')).toBe(true);
    });

    it('should clear selection', () => {
      const { selectItem, clearSelection } = useUIStore.getState();
      selectItem('items', 'item1');
      selectItem('items', 'item2');
      clearSelection('items');
      
      const state = useUIStore.getState();
      expect(state.selections).toEqual({ items: new Set() });
    });
  });

  describe('View and Sort Management', () => {
    it('should manage view mode', () => {
      const { setViewMode } = useUIStore.getState();
      setViewMode('results', 'grid');
      
      const state = useUIStore.getState();
      expect(state.viewMode).toEqual({ results: 'grid' });
    });

    it('should manage sort state', () => {
      const { setSort } = useUIStore.getState();
      setSort('results', 'date', 'desc');
      
      const state = useUIStore.getState();
      expect(state.sortBy).toEqual({ results: 'date' });
      expect(state.sortOrder).toEqual({ results: 'desc' });
    });

    it('should toggle sort order', () => {
      const { setSort, toggleSort } = useUIStore.getState();
      setSort('results', 'name', 'asc');
      toggleSort('results', 'name');
      
      const state = useUIStore.getState();
      expect(state.sortOrder).toEqual({ results: 'desc' });
    });
  });

  describe('Modal Management', () => {
    it('should open modal', () => {
      const { openModal } = useUIStore.getState();
      openModal('settings', { userId: '123' });
      
      const state = useUIStore.getState();
      expect(state.activeModal).toBe('settings');
      expect(state.modalData).toEqual({ userId: '123' });
    });

    it('should close modal', () => {
      const { openModal, closeModal } = useUIStore.getState();
      openModal('settings');
      closeModal();
      
      const state = useUIStore.getState();
      expect(state.activeModal).toBe(null);
      expect(state.modalData).toBe(null);
    });

    it('should manage modal stack', () => {
      const { openModal, closeModal } = useUIStore.getState();
      openModal('first');
      openModal('second');
      closeModal();
      
      const state = useUIStore.getState();
      expect(state.activeModal).toBe('first');
    });
  });

  describe('Utility Functions', () => {
    it('should reset UI state', () => {
      const { setActiveTab, setSearchQuery, setFilter, resetUI } = useUIStore.getState();
      
      // Set some state
      setActiveTab('analytics');
      setSearchQuery('test');
      setFilter('category', 'politics');
      
      resetUI();
      
      const state = useUIStore.getState();
      expect(state.activeTab).toBe('feed');
      expect(state.searchQuery).toBe('');
      expect(state.filters).toEqual({});
    });

    it('should reset specific sections', () => {
      const { setFormData, setFormError, resetSection } = useUIStore.getState();
      setFormData('name', 'John');
      setFormError('email', 'Invalid');
      resetSection('formData');
      
      const state = useUIStore.getState();
      expect(state.formData).toEqual({});
      expect(state.formErrors).toEqual({});
      expect(state.formTouched).toEqual({});
    });
  });
});